<template>
<section class="section main-section">
    

    <div v-if="!isWebCamLive">
    <div v-if="webcams.length">
    <div class="container box">
    <h2 class="subtitle"> Global Recording Options</h2>
 <b-field style="justify-content:center;" grouped group-multiline>
            <div class="control">
                <b-switch :loading="isEncodingLoading" :disabled="isEncodingLoading" @input="changeEncoding" v-model="isEncoding">Encoding</b-switch>
            </div>
            <div class="control">
                <b-switch  :loading="isGenNameLoading" :disabled="isGenNameLoading"  @input="changeNaming"  v-model="isGenName">Auto VIDEO Naming</b-switch>
            </div>
           <b-tooltip type="is-info" label="Available only when encoding is on." always :active="!isEncoding" position="is-bottom"
            size="is-small"
            multilined
            >
            <div class="control">
                <b-switch :loading="isStreamLoading" :disabled="isStreamLoading || (!isEncoding)" @input="changeisStream" v-model="isStream">Stream video while recording</b-switch>
            </div>
           </b-tooltip>
</b-field>

        <b-field label="Frame Rate" style="max-width:20rem;margin-left:auto;margin-right:auto;margin-top:4rem;margin-bottom:3rem;">
            <b-numberinput @input="changeFrameRate" :loading="isFrameRateLoading" :disabled="isFrameRateLoading" v-model="isFrameRate" placeholder="Frame Rate" min=5 step=1>
            </b-numberinput>
        </b-field>
    </div>


    <div class="container box">
     <h2 class="subtitle"> Continuous Recording </h2>
         <transition name="fade">
        <b-field v-if="!isGenName" label='Video Name ( no _ and invalid *nix fileNames characters)'>
            <b-input v-model="inputVidName"  :disabled="recordingOn"></b-input>
        </b-field>
         </transition>
                
                <b-button
                type="is-warning"  
                :loading="loading_start"
                @click="handleStartContRecord()"
                :disabled="recordingOn"
                >
                <b-icon
                icon="checkbox-blank-circle"
                size="is-small"
                type="is-success">
            </b-icon> Start Recording
            </b-button>
                      <b-button
                type="is-warning"  
                :loading="loading_stop"
                @click="handleStopContRecord()"
                style="margin-left:1rem"
                :disabled="!recordingOn"
                >
                <b-icon
                icon="checkbox-blank-circle"
                size="is-small"
                type="is-danger">
            </b-icon> Stop Recording
            </b-button>
     </div> 
     
    <div v-if="recordingOn">
          <b-notification
            :type="(!loading_stop) ? 'is-info' : 'is-dark'"
            has-icon
            class="not-box"
            :closable="false">
            <div class="not-txt"> {{contNotRecMsg}} <div class="dot-flashing" style="display: inline-block;margin-left: 2rem;"></div></div>
        </b-notification>
     </div>
     
     </div>
     <div v-else>
           {{noWebcamsMsg}}
     </div>
    </div><div v-else>
          <b-notification
            :type="'is-dark'"
            class="not-box"
            :closable="false">
            <div class="not-txt"> Webcam busy from <b>View Live</b> tab wait a bit for release <div class="dot-flashing" style="display: inline-block;margin-left: 2rem;"></div>
            <p><small>You can view live recording in the video files by enabiling streaming, not recomanded on slow hardware.</small></p>
            </div>
        </b-notification>

    </div>

  </section>
</template>


<script>

export default {
  name: 'RecordVideo',
  
  data () {
    return {
    isGenName:true,
    isGenNameLoading:false,
    isEncoding:true,
    isEncodingLoading:false,
    isStream:false,
    isStreamLoading:false,
    isFrameRate:10,
    isFrameRateLoading:false,
    contNotRecMsg: '',

    isWebCamLive:false,
    checkWebCamLiveIntval:undefined,
    storeSubStop:undefined,
    inputVidName:'',
    error:false,
    loading_start:false,
    loading_stop:false,
    errorMsg:'',
    recordingOn: false,
    apiInUse:false,
    imgSrc:this.$axios.defaults.baseURL + this.$axios.defaults.apiEndpoints['live-video-feed'] +'?t=' + new Date().getTime(),
    webcams:[],

    }
  },
  props: {
    msg: String
  },
  created(){

    import('./../util/helpers/toastHelper').then(
      (toastHelper) => {
        this.toastHelper = toastHelper
      }
    )

    this.loading_stop = this.$store.state.isStopRecording;
    
    if(this.loading_stop){
      this.contNotRecMsg = 'Waiting for WebCam release.';
     //_state
  this.storeSubStop = this.$store.subscribe((mutation) => {
  if( mutation.type === 'set_stopRecording'){
    if(this.$store.state.isStopRecording === false){
      this.loading_stop = false;
      this.recordingOn = false;
      this.storeSubStop();
    }
  }
 
})
    }else{
      this.contNotRecMsg = 'WebCam is now recording.';
    }

 
    this.noWebcamsMsg ='No webcams detected please conect one and recheck.';

    
     this.$axios.get(this.$axios.defaults.apiEndpoints['live-video-check']).then(
          (res) => {
        this.isWebCamLive = res.data.live;
    }
     )



     this.$axios.get(this.$axios.defaults.apiEndpoints['record-get-options']).then(
       (resp) =>
       {
    
    this.isGenName = Boolean(resp.data.autoNaming).valueOf()
    this.isEncoding = Boolean(resp.data.encoding).valueOf()
    this.isStream  = Boolean( resp.data.stream).valueOf()
    this.isFrameRate  = Number(resp.data.frameRate).valueOf()


      this.$axios.get(this.$axios.defaults.apiEndpoints['record-status']).then(
          (res) => {
        this.recordingOn = res.data.recording;
    
    
            if(this.recordingOn && !this.isGenName ){
     this.$axios.get(this.$axios.defaults.apiEndpoints['get-last-video-name']).then(
          (res) => {
        if(! res.data.error ){
 
        this.inputVidName = res.data.name.split('_')[1].split('_')[0];
        }
    }
     )

         }
    
    
    }
     )


 


       }

     )


   let checkLiveOn = async () => {
     let resp = await this.$axios.get(this.$axios.defaults.apiEndpoints['live-video-check']);
        
        this.isWebCamLive = resp.data.live;
        if( ! this.isWebCamLive && typeof(this.checkWebCamLiveIntval) !== undefined){
          clearInterval(this.checkWebCamLiveIntval);
        }
        return true;
    }

    checkLiveOn().then(
      () => {
         if(this.isWebCamLive){
           this.checkWebCamLiveIntval = setInterval(checkLiveOn, 130)
         }

      }


    );

    

    this.$axios.get(this.$axios.defaults.apiEndpoints['check-webcams']).then(
    (res) => {
        this.webcams = res.data;
    }

    ).catch( () => {
        this.webcams = [];
    } );

 





  },
  beforeDestroy(){

    if(typeof(this.checkWebCamLiveIntval) !== 'undefined'){
      clearInterval(this.checkWebCamLiveIntval);
    } 

    if(typeof(this.storeSubStop) == 'function'){
      this.storeSubStop()
    }

  },
  methods:{
async handleStartContRecord() {
if(this.apiInUse) return;
this.loading_start = true;
this.apiInUse = true;

try {

    let axiosParams = {}
    if( ! this.isGenName ){
      axiosParams = { params:{ 'vidName':this.inputVidName } }
    }
     

     let resp = await this.$axios.get(this.$axios.defaults.apiEndpoints['record-video'], axiosParams);
     if(! resp.data.error){
        this.recordingOn = true;        
     }else{
       this.toastHelper.showToastError(resp.data.msg, this.$buefy)
     }

     this.loading_start = false;
     this.apiInUse = false;
 }catch( _error ){
this.toastHelper.showToastError(_error.toString(), this.$buefy)
this.loading_start = false;
this.apiInUse = false;
 }
 
 
},
async handleStopContRecord(){
if(this.apiInUse) return;
this.loading_stop = true;
await this.$store.commit('set_stopRecording', this.loading_stop)
this.apiInUse = true;
try {
     await this.$axios.get(this.$axios.defaults.apiEndpoints['record-video-stop'], { timeout: 30000 } );
     this.loading_stop = false;
     await this.$store.commit('set_stopRecording', this.loading_stop)
     this.recordingOn = false;
     this.apiInUse = false;
 }catch( _error ){
this.loading_stop = false;
this.apiInUse = false;
 }
 
},
async changeEncoding(inputChange){

  this.isEncodingLoading = true;
 
 
        this.$axios
        .post(this.$axios.defaults.apiEndpoints["record-set-encoding"], {
          encoding: inputChange
        })
        .then(async resp => {
          if (!resp.data.error) {
            this.isEncoding = inputChange;
            if(! this.isEncoding) {
              await this.changeisStream(false);
            }
          } else {
            this.toastHelper.showToastError(resp.data.msg, this.$buefy);
          }
          this.isEncodingLoading = false;
        })
        .catch(e => {
          this.toastHelper.showToastError(e.toString(), this.$buefy);
          this.isEncodingLoading = false;
        });

},
async changeNaming(inputChange){

  this.isGenNameLoading = true;
 
 
        this.$axios
        .post(this.$axios.defaults.apiEndpoints["record-set-naming"], {
          naming: inputChange
        })
        .then(resp => {
          if (!resp.data.error) {
            this.isGenName = inputChange;
          } else {
            this.toastHelper.showToastError(resp.data.msg, this.$buefy);
          }
          this.isGenNameLoading = false;
        })
        .catch(e => {
          this.toastHelper.showToastError(e.toString(), this.$buefy);
          this.isGenNameLoading = false;
        });

},
async changeisStream(inputChange){

  this.isStreamLoading = true;
 
        this.$axios
        .post(this.$axios.defaults.apiEndpoints["record-set-is-stream"], {
          isStream: inputChange
        })
        .then(resp => {
          if (!resp.data.error) {
            this.isStream = inputChange;
          } else {
            this.toastHelper.showToastError(resp.data.msg, this.$buefy);
          }
          this.isStreamLoading = false;
        })
        .catch(e => {
          this.toastHelper.showToastError(e.toString(), this.$buefy);
          this.isStreamLoading = false;
        });

},
async changeFrameRate(inputChange){
this.isFrameRateLoading = true;

        this.$axios
        .post(this.$axios.defaults.apiEndpoints["record-set-framerate"], {
          framerate: Number(inputChange).valueOf()
        })
        .then(resp => {
          if (!resp.data.error) {
            this.isFrameRate = Number(inputChange).valueOf();
          } else {
            this.toastHelper.showToastError(resp.data.msg, this.$buefy);
          }
          this.isFrameRateLoading = false;
        })
        .catch(e => {
          this.toastHelper.showToastError(e.toString(), this.$buefy);
          this.isFrameRateLoading = false;
        });



},
  },
  computed: {
  onlineWebCamsFirst: function () {
     let webcams = this.webcams;
    return webcams.sort(( a, b )=> {
        if (a.active === b.active) {
         return  Number(a.camera[a.camera.length -1]).valueOf() - Number(b.camera[b.camera.length -1]).valueOf();
      }
         return b.active - a.active;
     })
  },
  onlyOnlineWebcams(){
      return this.webcams.filter((webcam)=> {
          return webcam.active
      })
  }
}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
 figure {
    margin-top: 1.5rem;
 }

</style>
